html,
body,
ul,
li,
h1,
h2,
h3,
input,
textarea {
  margin: 0;
  padding: 0;
  border-width: 0;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 27px; }

a {
  color: #0075ff;
  text-decoration: none; }

.header {
  height: 700px;
  background-image: url("../images/header_new.svg");
  background-size: cover;
  background-position: center;
  position: relative;
  font-weight: bold;
  line-height: 16px; }
  .header__name {
    position: absolute;
    top: 314px;
    left: 120px;
    bottom: 165px;
    font-size: 60px;
    line-height: 60px;
    letter-spacing: -1.5px;
    color: white; }
  .header__name::before {
    content: "";
    display: block;
    width: 35px;
    height: 2px;
    border-radius: 2px;
    background: white;
    margin-bottom: 16px; }
  .header__nav {
    position: absolute;
    bottom: 48px;
    left: 0;
    right: 0; }
  .header__top {
    position: relative;
    display: flex;
    justify-content: space-between;
    top: 24px;
    color: white; }
  .header__phone {
    margin-left: 120px; }
  .header__action {
    margin-right: 120px; }
  .header__phone, .header__action {
    position: relative;
    top: 32px; }
  .header__link {
    color: white;
    text-decoration: none;
    border-bottom: 1px solid white;
    transition: color 0.2s;
    text-transform: uppercase; }
  .header__link:hover {
    color: #9fadc2; }
  @media screen and (max-width: 768px) {
    .header__phone, .header__action {
      display: none; }
    .header__name {
      position: relative;
      left: 20px;
      bottom: 80px; } }

nav {
  color: white;
  text-transform: uppercase; }
  nav__list {
    list-style: none;
    display: flex;
    justify-content: center; }
  nav__item {
    padding: 8px 24px;
    margin: 0 26px; }
  nav__item_active {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px; }
  nav__link {
    color: white;
    text-decoration: none;
    transition: color 0.2s;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px; }
  nav__link:hover {
    color: #9fadc2; }
  @media screen and (max-width: 768px) {
    nav__list {
      display: none; } }

.logo {
  width: 115px;
  height: 76px;
  background-image: url("../images/logo.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center; }
  @media screen and (max-width: 768px) {
    .logo {
      width: 73px;
      height: 48px; } }

.section {
  padding: 104px calc(50% - 510px);
  position: relative;
  background: white; }
  .section__heading {
    left: 0;
    right: 0;
    top: -0.5em;
    margin: auto; }
  @media screen and (max-width: 768px) {
    .section {
      padding-top: 64px;
      padding-left: 20px;
      padding-right: 20px; } }
  @media screen and (max-width: 500px) {
    .section__heading {
      font-size: 36px;
      line-height: 36px; } }

.info {
  display: flex;
  justify-content: space-between; }
  .info__heading {
    width: 328px;
    font-weight: bold;
    font-size: 48px;
    line-height: 48px;
    letter-spacing: -1.5px; }
  .info__text {
    max-width: 500px; }
  .info__small_heading {
    color: #0075ff;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 16px; }
  @media screen and (max-width: 999px) {
    .info {
      flex-direction: column; }
    .info__heading {
      margin-bottom: 48px; } }

.heading {
  font-size: 48px;
  line-height: 48px;
  position: absolute;
  text-transform: uppercase;
  width: max-content; }
  .heading__crown {
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    width: 23px;
    height: 16px;
    top: calc(100% + 7px);
    margin: auto;
    background-image: url(../images/crown.svg);
    background-repeat: no-repeat; }

.photos {
  display: flex;
  justify-content: space-between; }
  .photos__img {
    width: calc(50% - 10px);
    height: 256px;
    object-fit: cover;
    object-position: center;
    border-radius: 8px; }
  .photos_wide_left__img:first-child {
    width: calc(67% - 10px); }
  .photos_wide_left__img:last-child {
    width: calc(33% - 10px); }
  .photos__wide_right__img:first-child {
    width: calc(33% - 10px); }
  .photos__wide_right__img:last-child {
    width: calc(67% - 10px); }
  @media screen and (max-width: 999px) {
    .photos__img {
      height: 131px; } }

.gallery__caption {
  margin: 24px 0 40px; }
  .gallery__caption__button {
    margin: 40px auto 0; }

.button {
  display: block;
  line-height: 14px;
  font-weight: bold;
  color: #0075ff;
  background-color: white;
  cursor: pointer;
  padding: 21px 66px;
  border-radius: 8px;
  border: 2px solid #0075ff;
  transition: background-color 0.2s; }
  .button:hover {
    color: white;
    background-color: #0075ff; }

.link {
  font-weight: bold;
  transition: color 0.2s; }
  .link:hover {
    color: #0200ff; }

.consultation {
  display: flex;
  justify-content: space-between; }
  .consultation__heading {
    font-size: 24px;
    line-height: 30px;
    color: #0075ff;
    margin-bottom: 16px; }
  .consultation__form {
    width: 413px; }
  .consultation__text {
    width: 412px; }
  @media screen and (max-width: 999px) {
    .consultation {
      flex-direction: column; }
    .consultation__form, .consultation__text {
      width: 100%; } }

.form {
  display: flex;
  flex-direction: column; }
  .form__item {
    margin-bottom: 16px; }
  .form__input, .form__button {
    width: 100%; }

.input {
  font-size: 14px;
  line-height: 14px;
  padding: 17px;
  background-color: #f7f8ff;
  border: 1px solid #fff;
  border-radius: 8px;
  box-sizing: border-box; }
  .input::placeholder {
    color: #b9c1ce; }
  .input__textarea {
    height: 144px; }

.testimonials {
  display: grid;
  grid-template-columns: repeat(3, 240px);
  grid-gap: 150px;
  justify-content: center; }
  @media screen and (max-width: 999px) {
    .testimonials {
      grid-template-columns: 280px; } }

.person {
  text-align: center; }
  .person__name {
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    color: #0075ff;
    margin: 28px auto 4px; }
  .person__position {
    font-size: 14px;
    color: #9fadc2;
    margin: 0; }
  .person__text {
    color: #3e3e3e; }

.contact {
  display: flex;
  justify-content: space-between; }
  .contact__text {
    display: grid;
    grid-template-columns: repeat(2, 152px);
    grid-template-rows: repeat(4, 27px);
    grid-auto-flow: column;
    grid-gap: 0 108px;
    color: #3e3e3e;
    width: 152px; }
  .contact__names {
    font-size: 14px;
    color: #9fadc2;
    margin-bottom: 8px; }
  .contact__form {
    width: 413px; }
  .contact__link {
    color: #3e3e3e; }

